html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.site-header {
  box-sizing: border-box;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 70px;
  padding: 0;
  display: flex;
}

.logo img {
  width: auto;
  height: 50px;
  margin-left: 20px;
  display: block;
}

.main-nav {
  display: block;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  color: #000;
  text-align: center;
  height: 70px;
  margin-right: 20px;
  line-height: 70px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav-item:first-child {
  margin-left: 0;
}

.nav-item:last-child {
  margin-right: 0;
}

.nav-item:hover, .nav-item.is-active {
  color: #00acdc;
}

.nav-item.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
